import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BlogComponent } from './blog/blog.component';


import { ClassicLeftSidebarComponent } from './blog/blog-grid/classic-left-sidebar/classic-left-sidebar.component';
import { TvmComponent } from './blog/tvm/tvm.component';


export const routes: Routes = [
  {
    path: '',
    redirectTo: 'agency',
    pathMatch: 'full',
  },
  {
    path: 'landing',
    component: ClassicLeftSidebarComponent
  },
  {
    path: 'blog',
    component: BlogComponent,
    loadChildren: () => import('./blog/blog.module').then(m => m.BlogModule),
    data: {
      breadcrumb: "Blog"
    }
  },
  {
    path: 'tvm',
    component: TvmComponent,
    loadChildren: () =>import('./blog/tvm/tvm.module').then(m => m.TvmModule)
  },
 
 


 
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    // preloadingStrategy: PreloadAllModules,
    anchorScrolling: 'enabled',
    scrollPositionRestoration: 'enabled'
})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
