import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import * as firebase from 'firebase';



@Component({
  selector: 'app-enq',
  templateUrl: './enq.component.html',
  styleUrls: ['./enq.component.scss']
})
export class EnqComponent implements OnInit {

  form: FormGroup;
  createFormGroup: any;

  constructor(
    private afs: AngularFirestore,
    private toastr: ToastrService
  ) { 
    this.form = new FormGroup({
      'name': new FormControl('ajay venugopal', [
        Validators.required
      ]),
      'phone': new FormControl('', [
        Validators.required,

      ]),
      'email': new FormControl('example@mail.com', [
        Validators.required,
        Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$")
      ]),
      'subject': new FormControl('',[
        Validators.required
      ]),
      'message': new FormControl('',[
          Validators.required
      ] )
    })
   
  }

  ngOnInit(): void {
  }
  submit() {
    console.log(this.form.value.subject),
      console.log(this.form.value.phone),
      console.log(this.form.value.email),
      console.log(this.form.value.name),
      console.log(this.form.value.message)
    if (this.form.status === 'INVALID') {
      this.toastr.error("Please enter  valid details");
      return false;
    }
    this.toastr.success('we would contact you soon');
    this.afs.collection('contact').add({
      subject: this.form.value.subject,
      phone: this.form.value.phone,
      email: this.form.value.email,
      name: this.form.value.name,
      message: this.form.value.message,
      time: firebase.firestore.FieldValue.serverTimestamp()
    })
  }

}
